import React, { useState } from "react";
import {
  Button,
  Card,
  Col,
  Divider,
  Input,
  Row,
  Tag,
  message,
  Select,
} from "antd";
import { publish_job_api } from "../net/netJob";
import * as _ from "lodash";

const { TextArea } = Input;
const { Option } = Select;

// job_name  // job_category

// company_name  // min_education

// company_tele  // company_email

// min_salary  // max_salary

// job_describe

// job_demand

export let name_chinese = {
  job_name_: "",
  job_category_: "",
  company_name_: "",
  min_education_: "",
  company_tele_: "",
  company_email_: "",
  min_salary_: "",
  max_salary_: "",
  job_describe_: "",
  job_demand_: "",
  job_create_time_: "",
  job_status_: "",
};

export let min_education_view = (default_value: string, f: any) => {
  let min_education_selects = [
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
  ];
  let select_list = min_education_selects.map((item) => {
    return (
      <Option key={item} value={item}>
        {item == "" ? "" : item}
      </Option>
    );
  });

  return (
    <Row>
      <Divider type="vertical"></Divider>
      <Tag color="red"> {name_chinese["min_education_"]} </Tag>
      <Select
        defaultValue={default_value}
        style={{ width: 120 }}
        onChange={(value) => {
          f(value);
        }}
      >
        {select_list}
      </Select>
      <Divider type="vertical"></Divider>
    </Row>
  );
};

export let RealseJobInfoPanel = () => {
  let [job_name, set_job_name] = useState("");
  let [job_category, set_job_category] = useState("");
  let [company_name, set_company_name] = useState("");
  let [min_education, set_min_education] = useState("");
  let [company_tele, set_company_tele] = useState("");
  let [company_email, set_company_email] = useState("");
  let [min_salary, set_min_salary] = useState("");
  let [max_salary, set_max_salary] = useState("");
  let [job_describe, set_job_describe] = useState("");
  let [job_demand, set_job_demand] = useState("");

  let clear_info = () => {
    set_job_name("");
    set_job_category("");
    set_company_name("");
    set_min_education("");
    set_company_tele("");
    set_company_tele("");
    set_company_email("");
    set_min_salary("");
    set_max_salary("");
    set_job_describe("");
    set_job_demand("");
  };

  let maxHeight = document.body.clientHeight * 0.9;
  let leftInputView = () => {
    return (
      <Col>
        <Divider dashed={true}></Divider>
        <Input
          addonBefore={name_chinese["job_name_"]}
          value={job_name}
          onChange={(event) => {
            let default_len = 100;
            let textValue = event.target.value;

            if (textValue.length > default_len) {
              set_job_name("");
              let config = {
                content: (
                  <Row>
                    {name_chinese["job_name_"]}  {default_len} 
                  </Row>
                ),
                duration: 3,
              };
              message.error(config);
            } else {
              set_job_name(textValue);
            }
          }}
        ></Input>

        <Divider dashed={true}></Divider>
        <Row>
          <Input
            addonBefore={name_chinese["company_name_"]}
            value={company_name}
            onChange={(event) => {
              let default_len = 100;
              let textValue = event.target.value;

              if (textValue.length > default_len) {
                set_company_name("");
                let config = {
                  content: (
                    <Row>
                      {name_chinese["company_name_"]}  {default_len} 
                    </Row>
                  ),
                  duration: 3,
                };
                message.error(config);
              } else {
                set_company_name(textValue);
              }
            }}
          ></Input>
        </Row>
        <Divider dashed={true}></Divider>
        <Row>
          <Input
            addonBefore={name_chinese["company_tele_"]}
            value={company_tele}
            onChange={(event) => {
              let default_len = 100;
              let textValue = event.target.value;

              if (textValue.length > default_len) {
                set_company_tele("");
                let config = {
                  content: (
                    <Row>
                      {name_chinese["company_tele_"]}  {default_len} 
                    </Row>
                  ),
                  duration: 3,
                };
                message.error(config);
              } else {
                set_company_tele(textValue);
              }
            }}
          ></Input>
        </Row>
        <Divider dashed={true}></Divider>
        <Row>
          <Input
            addonBefore={name_chinese["min_salary_"]}
            value={min_salary}
            onChange={(event) => {
              let default_len = 100;
              let textValue = event.target.value;

              if (textValue.length > default_len) {
                set_min_salary("");
                let config = {
                  content: (
                    <Row>
                      {name_chinese["min_salary_"]}  {default_len} 
                    </Row>
                  ),
                  duration: 3,
                };
                message.error(config);
              } else {
                set_min_salary(textValue);
              }
            }}
          ></Input>
        </Row>
        <Divider dashed={true}></Divider>
      </Col>
    );
  };
  let rightInputView = () => {
    return (
      <Col>
        <Divider dashed={true}></Divider>
        <Row>
          <Input
            addonBefore={name_chinese["job_category_"]}
            value={job_category}
            onChange={(event) => {
              let default_len = 100;
              let textValue = event.target.value;

              if (textValue.length > default_len) {
                set_job_category("");
                let config = {
                  content: (
                    <Row>
                      {name_chinese["job_category_"]}  {default_len} 
                    </Row>
                  ),
                  duration: 3,
                };
                message.error(config);
              } else {
                set_job_category(textValue);
              }
            }}
          ></Input>
        </Row>
        <Divider dashed={true}></Divider>
        <Row>{min_education_view("", set_min_education)}</Row>
        <Divider dashed={true}></Divider>
        <Row>
          <Input
            addonBefore={name_chinese["company_email_"]}
            value={company_email}
            onChange={(event) => {
              let default_len = 100;
              let textValue = event.target.value;

              if (textValue.length > default_len) {
                set_company_email("");
                let config = {
                  content: (
                    <Row>
                      {name_chinese["company_email_"]}  {default_len} 
                    </Row>
                  ),
                  duration: 3,
                };
                message.error(config);
              } else {
                set_company_email(textValue);
              }
            }}
          ></Input>
        </Row>
        <Divider dashed={true}></Divider>
        <Row>
          <Input
            addonBefore={name_chinese["max_salary_"]}
            value={max_salary}
            onChange={(event) => {
              let default_len = 100;
              let textValue = event.target.value;

              if (textValue.length > default_len) {
                set_max_salary("");
                let config = {
                  content: (
                    <Row>
                      {name_chinese["max_salary_"]}  {default_len} 
                    </Row>
                  ),
                  duration: 3,
                };
                message.error(config);
              } else {
                set_max_salary(textValue);
              }
            }}
          ></Input>
        </Row>
        <Divider dashed={true}></Divider>
      </Col>
    );
  };

  let jobDescribeView = () => {
    let auto_config = { minRows: 5 };
    return (
      <Col>
        <Tag color="blue"> {name_chinese["job_describe_"]} </Tag>
        <TextArea
          autoSize={auto_config}
          value={job_describe}
          onChange={(event) => {
            let default_len = 1000;
            let textValue = event.target.value;

            if (textValue.length > default_len) {
              set_job_describe("");
              let config = {
                content: (
                  <Row>
                    {name_chinese["job_describe_"]}  {default_len} 
                  </Row>
                ),
                duration: 3,
              };
              message.error(config);
            } else {
              set_job_describe(textValue);
            }
          }}
        />
      </Col>
    );
  };
  let jobDemandView = () => {
    let auto_config = { minRows: 5 };
    return (
      <Col>
        <Tag color="blue"> {name_chinese["job_demand_"]} </Tag>
        <TextArea
          autoSize={auto_config}
          value={job_demand}
          onChange={(event) => {
            let default_len = 1000;
            let textValue = event.target.value;

            if (textValue.length > default_len) {
              set_job_demand("");
              let config = {
                content: (
                  <Row>
                    {name_chinese["job_demand_"]}  {default_len} 
                  </Row>
                ),
                duration: 3,
              };
              message.error(config);
            } else {
              set_job_demand(textValue);
            }
          }}
        />
      </Col>
    );
  };
  let fill_card_view = () => {
    return (
      <Card>
        <Col>
          <Row
            style={{
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
            }}
          ></Row>
          <Row
            style={{
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
            }}
          >
            {leftInputView()}
            <Divider dashed={true} type="vertical"></Divider>
            {rightInputView()}
          </Row>
          {jobDescribeView()}
          <Divider dashed={true}></Divider>
          {jobDemandView()}
          <Divider dashed={true}></Divider>
          <Button
            type="primary"
            disabled={job_name == ""}
            onClick={async () => {
              let params = {
                job_name: job_name,
                job_category: job_category,
                company_name: company_name,
                min_education: min_education,
                company_tele: company_tele,
                company_email: company_email,
                min_salary: min_salary,
                max_salary: max_salary,
                job_describe: job_describe,
                job_demand: job_demand,
              };
              let data = await publish_job_api(params);
              if (_.isEqual("0", _.get(data, "code"))) {
                // console.log("", data);
                message.info("");
                clear_info();
              }
            }}
          >
            {job_name == "" ? "" : ""}
          </Button>
        </Col>
      </Card>
    );
  };

  let upload_job_file = () => {};

  return (
    <Row
      style={{
        width: "100%",
        minHeight: maxHeight,
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
      }}
    >
      {fill_card_view()}
    </Row>
  );
};
