import React, { useEffect, useState } from "react";
import { Button, Col, Divider, Row, Upload, Tag, message } from "antd";
import { PlusOutlined } from "@ant-design/icons";
import * as _ from "lodash";
import {
  get_headimg_name_api,
  get_resume_file_name_api,
  upload_headimg_api,
} from "../net/netUser";
import { global_var } from "../global/userInfo";
import { PassThrough } from "stream";
import { downloadResByFileName } from "../net/netDownloadFile";
import { duration } from "moment";
import { nginx_download_url } from "../net/netConfig";

const { Dragger } = Upload;

function getBase64(img, callback) {
  const reader = new FileReader();
  reader.addEventListener("load", () => callback(reader.result));
  reader.readAsDataURL(img);
}

function beforeUpload(file) {
  const isJpgOrPng = file.type === "image/jpeg" || file.type === "image/png";
  if (!isJpgOrPng) {
    message.error("You can only upload JPG/PNG file!");
  }
  const isLt2M = file.size / 1024 / 1024 < 2;
  if (!isLt2M) {
    message.error(" 2MB!");
  }
  return isJpgOrPng && isLt2M;
}

// 
export let UploadHeadImgPanel = () => {
  let [fn, set_fn] = useState("");
  let [f_size, set_f_size] = useState(0);
  let [resume_file, set_resume_file] = useState("");

  let [image_url, set_image_url] = useState("");

  let get_file_name = async () => {
    let params = {
      user_uuid: _.get(global_var, "user_info.user_uuid", "null"),
    };
    let data = await get_headimg_name_api(params);
    //  console.log("=remote file name", data);

    let file_url =
      nginx_download_url +
      _.get(global_var, "user_info.user_uuid", "") +
      "/" +
      "headimg" +
      "/" +
      _.get(data, "data.file_name", "");
    set_image_url(file_url);
    console.log("  ", data);
    console.log(" url ", file_url);
  };

  useEffect(() => {
    get_file_name();
  }, [resume_file]);

  const props = {
    name: "input",
    accept: ".jpg,.png",
    beforeUpload: (file: any, fileList: any) => {
      let async_upload = async () => {
        let file_name = _.get(file, "name", "");
        set_fn(file_name);
        set_resume_file(file);
        let formData = new FormData();
        formData.set("file_name", file_name);
        formData.set("file", file);
        formData.set(
          "user_uuid",
          _.get(global_var, "user_info.user_uuid", "null")
        );
        await upload_headimg_api(formData);
        setTimeout(() => {
          get_file_name();
        }, 1000);
      };

      let check_head_img = async () => {
        const isJpgOrPng =
          file.type === "image/jpeg" || file.type === "image/png";
        if (!isJpgOrPng) {
          message.error(" jpg png ", 3);
        }
        const isLt2M = file.size / 1024 / 1024 < 2;
        if (!isLt2M) {
          message.error(" 2MB!", 3);
        }
        if (isLt2M && isJpgOrPng) {
          getBase64(file, (image_url) => set_image_url(image_url));
          async_upload();
        }
      };
      check_head_img();

      // async_upload();
      return false;
    },
    showUploadList: false,
    //   action: 'https://www.mocky.io/v2/5cc8019d300000980a055e76',
    onChange(info: any) {
      const { status } = info.file;
      if (status !== "uploading") {
        // let fileName = _.get(info, "file.name", "");
        // let fileSize = _.get(info, "file.size", 0);
        // set_fn(fileName);
        // set_f_size(fileSize);
      }
    },
  };

  let upload_headimg_file = () => {
    const uploadButton = (
      <div>
        <PlusOutlined />  <br /> .jpg,.png
      </div>
    );
    return (
      <Col style={{ width: "200px", height: "250px" }}>
        <Dragger
          {...props}
          style={{
            width: "200px",
            height: "250px",
            padding: "0",
            margin: "0",
          }}
        >
          {_.isEqual("", image_url) ? (
            uploadButton
          ) : (
            <img src={image_url} width={"200px"} height={"250px"} alt=""></img>
          )}
        </Dragger>
      </Col>
    );
  };

  return (
    <Row gutter={10} justify="center" style={{ width: "100%" }}>
      <Col>
        <Row style={{ fontSize: "larger" }}>  </Row>
        {upload_headimg_file()}
      </Col>
    </Row>
  );
};
