import * as _ from "lodash";
import React from "react";
import { message, Card } from "antd";
message.config({
  top: 100,
  duration: 1,
  maxCount: 1,
  rtl: true,
});

export let dealResp = (resq: any) => {
  // console.log(resq);
  let config: any = { top: 100, duration: 1 };
  if (
    _.isEqual(_.get(resq, "status", -1), 200) &&
    _.isEqual(_.get(resq, "data.code", "-1"), "0")
  ) {
    config["content"] = <pre></pre>;
    message.success(config);
  } else if (
    _.isEqual(_.get(resq, "status", -1), 200) &&
    _.isEqual(_.get(resq, "data.code", "-1"), "-1")
  ) {
    config["content"] = (
      <Card title="">{_.get(resq, "data.message", "")}</Card>
    );
    message.error(config);
  } else if (!_.isEqual(_.get(resq, "status", -1), 200)) {
    config["content"] = (
      <Card title="">{_.get(resq, "data.message", "")}</Card>
    );
    message.error(config);
  }
};
